form{
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: var(--bg);
    margin-bottom: 26px;
}

textarea{
    background-color: #F9FBFC;
    border: 2px solid var(--bg);
    border-radius: 0.25em;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea::placeholder{
    color:#A5A5A5;
    font-family: 'Roboto';
    font-weight: 400;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button{
    cursor:pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 800;
    width: 120px;
    height: 46px;
    border-radius: 0.25em;
    border: solid 0.5px var(--bg);
    font-family: 'Roboto';
}

.TodoForm-button--cancel{
    background: transparent;
    color:var(--bg);
}
.TodoForm-button--add{
    background-color: var(--bg);
    color: white;
    box-shadow: 0px 5px 25px rgba(97, 128, 250, 0.5); 
}